

import React from 'react'
import '../Styles/App.css';
import '../Styles/mediaquery.css'
import {  Route,Switch,NavLink,Link,useRouteMatch } from 'react-router-dom';
import About from './about';
import Sidebar from './concepts';
import Home from './home';
import {Nav,Navbar,NavDropdown} from 'react-bootstrap'
import Notfound from './Notfound';
import Contents from './contents';
import Signin from './sign-in';
import ReactRedux from "./reactredux";
import ReactNext from './reactnext';
import ReactType from "./reacttype";
import Mamo from "./mamo"

 function Navb() {
  

    return (
        <div>
            <Navbar  bg="black" variant="dark" sticky="top" expand="md" collapseOnSelect  >
            
<Navbar.Toggle aria-controls="basic-navbar-nav"/>
<Navbar.Collapse id='Na-dropdown'>
  <Nav >
    
        <Nav.Link href="#aab" >  <li  ><NavLink to={'/sign-in'} className="nav-link navb-link" > Sign in </NavLink ></li></Nav.Link>
        <Nav.Link>  <li className="nav-item" ><navLink to='/'  className="nav-link navb-link" > Home </navLink></li></Nav.Link>



       
      
     
<NavDropdown title='Concepts ' id='Nav-dropdown'  >
<NavDropdown.Item className='navdropdown' href='#concepts/bbb'><NavLink exact to={`/reactredux`} ><div  >React and Redux</div></NavLink></NavDropdown.Item>
<NavDropdown.Item className='navdropdown' href='#bb'><NavLink to={`/reactnext`} className=' NavDropdown.Item'><div > React and Next</div></NavLink></NavDropdown.Item>
<NavDropdown.Item className='navdropdown' href='#bub'><NavLink to={`/reacttype`} className='NavDropdown.Item'><div > React and Typescript</div></NavLink></NavDropdown.Item>

</NavDropdown>



     
       
        
        
        
        
        
        
        <Nav.Link href="#aaf">    <li className="nav-item"><NavLink  to={'/content'} className="nav-link navb-link" >Contents</NavLink></li></Nav.Link>
        <Nav.Link href="#aag">   <li className="nav-item"><NavLink to={'/about'} className="nav-link navb-link" >About</NavLink></li></Nav.Link>
            
            </Nav>

<form className="form-inline   ml-auto " action="# " >
  <input className="form-control mr-sm-2 searchbar" type="text" placeholder="serach..."/>
  <button type="submit" className="btn btn-success searchbtn">Search</button>
</form>
  </Navbar.Collapse>
        
          
          
          
         




          </Navbar>

          
          
          <Switch>
          <Route exact path= '/reactredux' component={ReactRedux} />
          <Route path= '/reactnext' component={ReactNext} />
            <Route path='/reacttype' component={ReactType} />
            <Route path='/sign-in' component={Signin}/>
          <Route exact path='/' component={Home} />
         
          <Route path='/concepts' component={Sidebar} />
          <Route  path='/content' component={Contents} />
          
          <Route path='/about' component={About} />
         
          <Route component={Notfound} />
          
          </Switch>
        
          
          
      
        </div>
    )
}






export default Navb