import React from 'react';
import '../Styles/input.css'
import {NavLink, Route, useRouteMatch ,Switch} from 'react-router-dom';
import Login from './log-in';
import Signup from './sign-up';
import '../Styles/mediaquery.css'
function Signin(){
const{url,path}=useRouteMatch();
return(

<div className='container '>
    
<div className=" jumbotron input-container w-50 mx-auto mt-5 jum-css">
    <div className='d-flex justify-content-center pb-5'>
    <h2 className='p-3 '><NavLink exact to={`${url}`} className=' list-group-item list-group-item-action'><span className="span-btn">SIGN IN</span></NavLink></h2>
    <h2 className='p-3 '><NavLink to={`${url}/p`} className=' list-group-item list-group-item-action'> <span className="span-btn"> SIGN UP</span></NavLink></h2>
    </div>
    <Switch>

       
    <Route exact path= {`${path}`} component={Login}/>
     <Route  path={`${path}/p`} component={Signup}/>
     </Switch>   
</div>

</div>

)


}
export default Signin;