
import '../Styles/input.css'
import '../Styles/mediaquery.css'
function Login(){

return(

<form action="##">
<div className="form-group  ">


    <label for="user" className="label">Email Address:</label>
    <input type="email" className="form-control" required/>
    <label for="pass" className='mx-auto label' >Password:</label>
    <input type="password" className="form-control "  minLength='6' required />
    
    <button type='submit' className='btn btn-primary log-btn mx-auto  '>LOG IN</button>
    
</div>
</form>


)

}

export default Login