import React from "react";
import '../Styles/mediaquery.css'

class Signup extends React.Component{

    constructor(props){
        super(props)

        this.state={
            fname:'',
            lname:''
        }
    }

 
    render(){

return(

<form action="##" onSubmit={this.handelpush.bind(this)}>
<div className="form-group  ">
 
<div className="d-flex pb-4">

<input type='text'  className="form-control mr-1" placeholder="First Name" value={this.state.fname} onChange={(e)=>this.setState({fname:e.target.value})}/>

<input type='text'  className="form-control ml-1 " placeholder="Lsat Name" value={this.state.lname} onChange={(e)=>this.setState({lname:e.target.value})}/>

</div>

    <label for="user" className="label">Email Address:*</label>
    <input type="email" className="form-control" required/>
    <label for="pass" className="label" >Password:*</label>
    <input type="password" className="form-control " minLength='3' maxLength='10' required/>
    
    <button type='submit' className='btn btn-primary w-100 mt-5  ' >GET STARTED</button>
    
</div>
</form>


)

}

handelpush() {

this.props.history.push('/');
alert('welcome' +" "+ this.state.fname +" "+this.state.lname +'!')

    };



}
export default Signup